body {
    margin: 0;
    padding: 0;
    background-color: #ffb6c140; /* #d1768f; Set the background color for dark mode */
    color: #000; /* Set the text color for dark mode */
    font-family: Arial, sans-serif; /* Set the font to Arial (fallback to sans-serif) */
}

.root {
    display: flex;
    place-content: center flex-start;
    align-items: center;
    /* background-color: rgb(0, 0, 0); */
    flex-flow: column nowrap;
    gap: 10px;
    height: 100vh;
    overflow: auto;
    padding: 0px;
    position: relative;
}

.container {
    display: flex;
    place-content: center flex-start;
    align-items: center;
    flex: 0 0 auto;
    flex-flow: column nowrap;
    gap: 10px;
    height: fit-content;
    max-width: 975px;
    overflow: auto;
    padding: 50px;
    position: relative;
    width: auto;
    gap: 64px;
}

.header {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    color: #000
}

.header-title {
    display: flex;
    gap: 12px;
}

.header-title h1 {
    width: 100%;
    font-weight: 900;
    font-size: 72px;
    letter-spacing: -2px;
    line-height: 1.2em;
    text-align: center;
    margin: 0;
}

.header-subtitle h2 {
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 1em;
    color: rgba(0, 0, 0, 0.5);
}

.form {
    background-color: #555; /* Set the form background color for dark mode */
    padding: 20px;
    width: 400px;
    margin: 0 auto;
    border: none;
    border-radius: 10px;
}

.demo {
    padding: 20px;
    width: 400px;
    margin: 0 auto;
    border: none;
}

.demo .assistant {
    width: 80%;
    float: left;
    background-color: #777;
    color: #fff;
    margin: 10px;
    padding: 10px;
    border-radius: 10px 10px 10px 0px;
}

.demo .user {
    width: 80%;
    float: right;
    background-color: #FF0080;
    color: #fff;
    margin: 10px;
    padding: 10px;
    border-radius: 10px 10px 0px 10px;
}

label {
    display: block;
    font-family: Arial, sans-serif; /* Set the font to Arial (fallback to sans-serif) */
    margin-bottom: 10px;
    color: #fff; /* Set the label text color for dark mode */
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ffffff50;
  opacity: 1; /* Firefox */
}

input[type="text"],
input[type="email"],
input[type="tel"],
select,
input[type="number"] {
    width: 100%;
    font-family: Arial, sans-serif; /* Set the font to Arial (fallback to sans-serif) */
    padding: 10px;
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
    box-sizing: border-box;         /* Opera/IE 8+ */
    margin-bottom: 2px;
    background-color: #666; /* Set the form input background color for dark mode */
    color: #fff; /* Set the form input text color for dark mode */
    border: none;
    border-radius: 5px;
}

button[type="submit"] {
    width: 100%;
    padding: 10px;
    background-color: #fff; /* Set the submit button background color for dark mode */
    color: #444; /* Set the submit button text color for dark mode */
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
